import "./libraries/cdn/gsap-custom-ease.js"; import "./libraries/cdn/iamport.js"; import "./libraries/cdn/jquery.js"; import "./libraries/custom/become-new.js"; import "./libraries/custom/calculate-position.js"; import "./libraries/custom/detect-mobile.js"; import "./libraries/custom/hex-to-rgb.js"; import "./libraries/custom/rgb-to-hex.js"; import "./libraries/custom/simple-pay.js"; import "./libraries/npm/axios.js"; import "./libraries/npm/gsap.js";





/*
  
*/

import app from './app.vue'
import './style.css';






/*
Vue  
*/
//  https://ui.toast.com/weekly-pick/ko_20200804
vue.use(vueTouchEvents);
vue.use(vueScroll,{
    ops: {
        vuescroll: {
            mode: 'native',
            sizeStrategy: 'percent',
            detectResize: true,
            wheelScrollDuration: 100,
            wheelDirectionReverse: false
        },
        scrollPanel: {
            scrollingX: false,
            scrollingY: true,
        },
        rail: {},
        bar: {
            showDelay: 1000,
            onlyShowBarOnScroll: true,
            keepShow: false,
            background: '#000000',
            opacity: 0.2,
            hoverStyle: false,
            specifyBorderRadius: false,
            minSize: 0,
            size: '6px',
            disable: false
        }
    },
    name: 'vue-scroll'
});
vue.use(vuetify);





/*
Vue  
*/

//     v-guide-alert 
vue.directive('guide-alert', {
    //    DOM  ...
    inserted: function (el, binding, vnode) {
        let showTimer;
        
        el.addEventListener('mouseover', function(){
            showTimer = setTimeout(function(){
                vnode.context.$root.showElasticAlert(el, binding.value);
            }, 500)
        })
        el.addEventListener('mouseleave', function(){
            clearTimeout(showTimer);
        })
        el.addEventListener('mousedown', function(){
            clearTimeout(showTimer);
        })
        el.addEventListener('click', function(){
            clearTimeout(showTimer);
        })
    }
})





/*
Vue  
*/

import helloWorld from './components/hello-world.vue'
vue.component('hello-world', helloWorld)
import topBar from './components/top-bar.vue';
